*,
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
}

input,
button,
textarea,
select {
  font-style: inherit;
}

button {
  cursor: pointer;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

ul,
li {
  list-style-type: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

body {
  font-family: "Roboto", sans-serif;
}

@font-face {
  font-family: "UniSansHeayBold";
  src: url("../fonts/UniSans.ttf");
  font-weight: bold;
  font-style: normal;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  background: url("../img/header/bg-header.jpg") top no-repeat;
}

.top-header__img {
  position: relative;
  z-index: 5;
}

.top-header__content {
  display: flex;
  align-items: center;
  height: 90px;
}

.top-header__column {
  flex: 0 1 50%;
}

.top-header__column:nth-child(2) {
  padding: 25px 0 0 0;
  flex: 0 1 190px;
  align-self: flex-start;
  text-align: center;
}

.menu__list {
  display: grid;
  grid-template-columns: 110px 130px 180px;
}

.menu__list li {
  margin: 1px 0;
}

.menu__link {
  display: inline-flex;
  justify-content: left;
  align-items: center;
  min-height: 27px;
  padding: 5px 22px;
  font-family: "Play", sans-serif;
  font-weight: bold;
  text-decoration: underline;
}

.menu__a {
  color: white;
}

.top-header__contacts {
  position: relative;
  z-index: 5;
}

.contacts-header {
  display: flex;
  justify-content: flex-end;
}

.contacts-header__column {
  border-right: 1px solid white;
  padding: 0 30px;
}

.contacts-header__column:last-child {
  border: 0;
  padding-right: 0;
}

.contacts-header__item--icon {
  background: url("../img/header/phone.png") left no-repeat;
  padding: 0 0 0 37px;
}

.contacts-header__label {
  font-size: 12px;
  font-weight: 300;
  color: white;
  margin-bottom: 5px;
}

.contacts-header__phone {
  color: white;
  font-weight: 900;
  font-size: 18px;
  white-space: nowrap;
}

.bottom-header__container {
  display: flex;
  height: 63px;
  align-items: center;
  justify-content: space-between;
}

.bottom-header__column {
  flex: 0 1 calc(50% - 95px);
}

.actions-header {
  display: flex;
  align-items: center;
}

.actions-header li {
  margin: 0 40px 0 0;
}

.actions-header__item {
  display: inline-block;
  line-height: 24px;
  padding: 0 0 0 33px;
  color: #999999;
  transition: 0.3s ease;
}

.actions-header__item span {
  border-bottom: 1px dotted;
}

.actions-header__item--login {
  background: url("../img/header/key.png") left no-repeat;
}

.actions-header__item--reg {
  background: url("../img/header/head.png") left no-repeat;
}

.actions-header__item--email {
  background: url("../img/header/email.png") left no-repeat;
}

.info-header {
  display: flex;
  justify-content: right;
  align-items: center;
}

.info-header__column {
  padding: 0 33px 0 0;
}

.info-header__column:last-child {
  padding: 0;
}

.info-header__callback {
  display: inline-block;
  background: url("../img/header/dotted-phone.png") left no-repeat;
  padding: 0 0 0 32px;
  line-height: 20px;
  color: #999999;
  transition: 0.3s ease;
}

.info-header__callback span {
  border-bottom: 1px dotted;
}

.info-header__schedule {
  display: inline-block;
  background: url("../img/header/schedule.png") left no-repeat;
  padding: 0 0 0 32px;
  line-height: 18px;
  color: #a1a1a1;
  text-align: center;
}

.info-header__days {
  font-weight: bold;
}

.info-header__car {
  position: relative;
  background: url("../img/header/car.png") center no-repeat;
  width: 43px;
  height: 43px;
  display: inline-block;
  line-height: 24px;
}

.info-header__car span {
  position: relative;
  right: -30px;
  padding: 3px 10px;
  line-height: 25px;
  border-radius: 20px;
  color: white;
  font-weight: 700;
  font-size: 18px;
  background-color: #f68038;
}

.icon__menu {
  display: none;
}

.page {
  padding: 60px 0 0 0;
  flex: 1 1 auto;
}

.page__container {
  display: flex;
}

.page__side {
  flex: 0 0 260px;
}

.page__content {
  flex: 1 1 auto;
  padding-left: 30px;
}

.menu-page {
  position: relative;
  background: url("../img/main/bg-aside.jpg") no-repeat;
}

.menu-page__header {
  padding: 20px;
  display: flex;
  align-items: center;
}

.menu-page__title {
  color: #ffffff;
  font-family: "UniSansHeayBold";
  font-size: 30px;
  letter-spacing: 0.75px;
  line-height: 116.6666666667%;
}

.menu-page__burger {
  flex: 0 0 36px;
  width: 36px;
  height: 36px;
  background-color: #068292;
  position: relative;
  cursor: pointer;
  padding: 10px 6px;
}

.menu-page__list li {
  border-bottom: 1px solid #1aa7b9;
  transition: 0.3s background-color linear;
}

.menu-page__link {
  position: relative;
  display: block;
  padding: 12px 20px;
  letter-spacing: 0.3px;
  font-weight: 300;
  font-size: 12px;
  color: white;
  cursor: pointer;
}

.menu-page__link--parent:after {
  content: "";
  background: url("../img/main/strelka.png") no-repeat;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
}

.menu-page__lines {
  position: relative;
  height: 16px;
}

.menu-page__lines .menu-page__span {
  transition: all 0.3s ease 0s;
  top: calc(50% - 1px);
  left: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: white;
}

.menu-page__lines .menu-page__span:first-child {
  top: 0;
}

.menu-page__lines .menu-page__span:last-child {
  top: auto;
  bottom: 0;
  height: 1.7px;
}

.menu-page__lines.__active .menu-page__span {
  transform: scale(0);
}

.menu-page__lines.__active .menu-page__span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}

.menu-page__lines.__active .menu-page__span:last-child {
  transform: rotate(45deg);
  top: calc(50% - 1px);
}

.submenu-page {
  position: absolute;
  top: 0;
  height: 100%;
  left: 100%;
  width: 280px;
}

.submenu-page .submenu-page__item ul.submenu-page__menu {
  padding: 20px 0;
}

.submenu-page .submenu-page__item ul.submenu-page__menu li a.submenu-page__link {
  font-size: 12px;
  text-align: left;
  font-weight: 200;
  letter-spacing: 0.3px;
  color: #1ab9ce;
  transition: 0.2s linear;
}

.submenu-page .submenu-page__item ul.submenu-page__menu li a.submenu-page__link:hover {
  color: white;
  background-color: #71c7d3;
}

@media (min-width: 991.98px) {
  .menu__link:hover {
    background-color: #068292;
    border-radius: 20px;
  }

  .actions-header__item:hover {
    color: #1ab9ce;
  }

  .info-header__callback:hover {
    color: #1ab9ce;
  }

  .menu-page__list li:hover {
    background-color: #098494;
  }
}

@media (max-width: 1152px) {
  .menu__list {
    grid-template-columns: 90px 110px 160px;
  }

  .menu__link {
    padding: 5px 10px;
  }

  .contacts-header__column {
    padding: 0 15px;
  }

  .contacts-header__phone {
    font-size: 16px;
  }

  .actions-header__item {
    padding: 0 0 0 27px;
  }

  .info-header__callback {
    background-image: none;
    padding: 0;
  }

  .info-header__schedule {
    background-image: none;
    padding: 0;
  }

  .info-header__car span {
    right: -15px;
  }
}

@media (max-width: 1140px) {
  .actions-header li {
    margin: 0 2.6315789474vw 0 0;
  }

  .info-header__column {
    padding: 0 2.8947368421vw 0 0;
  }
}

@media (max-width: 991.98px) {
  .menu__body {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    padding: 120px 20px 30px 20px;
    z-index: 2;
    overflow: auto;
    background-color: white;
    transition: all 0.4s ease 0s;
  }

  .menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    height: 105px;
    top: 0;
    left: 0;
    z-index: 3;
    background-color: #1ab9ce;
  }

  .menu__body.__active {
    opacity: 1;
    visibility: visible;
  }

  .menu__list {
    display: block;
  }

  .menu__list li {
    margin-bottom: 20px;
  }

  .menu__link {
    display: block;
    padding: 0;
  }

  .menu__a {
    color: black;
    font-size: 20px;
  }

  .contacts-header__column {
    border-right: none;
    padding: 0 7px 0 0;
  }

  .contacts-header__item--icon {
    padding: 0 0 0 30px;
  }

  .contacts-header__item--second {
    display: none;
  }

  .actions-header__item {
    padding: 0;
    background: none;
  }

  .info-header__schedule {
    display: none;
  }

  .icon__menu {
    position: relative;
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
  }

  .icon__menu span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: white;
  }

  .icon__menu span:first-child {
    top: 0;
  }

  .icon__menu span:last-child {
    top: auto;
    bottom: 0;
  }

  .icon__menu.__active span {
    transform: scale(0);
  }

  .icon__menu.__active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }

  .icon__menu.__active span:last-child {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }
}

@media (max-width: 767.98px) {
  .header {
    background: url("../img/header/bg-header.jpg") 0 0 no-repeat;
  }

  .top-header__img {
    width: 100px;
    position: relative;
  }

  .top-header__img img {
    max-width: 100%;
  }

  .menu__body {
    padding: 90px 20px 30px 20px;
  }

  .menu__body::before {
    height: 90px;
  }

  .contacts-header {
    justify-content: flex-end;
  }
}

@media (max-width: 640px) {
  .contacts-header__column {
    padding: 0;
    display: flex;
    align-items: center;
  }

  .contacts-header__item--icon {
    background: none;
  }

  .contacts-header__label {
    display: none;
  }

  .contacts-header__phone {
    font-size: 0;
    background: url("../img/header/phone.png") left no-repeat;
    width: 19px;
    height: 25px;
    display: inline-block;
    margin-right: 15px;
  }

  .actions-header {
    justify-content: space-between;
    margin-bottom: 15px;
  }

  .info-header__car span {
    font-size: 14px;
    line-height: 20px;
    padding: 0 5px;
    right: -25px;
  }
}

@media (max-width: 479.98px) {
  .top-header__img {
    width: 90px;
  }

  .contacts-header__item--icon {
    padding: 0;
  }

  .contacts-header__phone {
    margin-right: 5px;
  }
}