@import "vars.scss";

.page {
  padding: 60px 0 0 0;
  flex: 1 1 auto;

  &__container {
    display: flex;
  }

  &__side {
    flex: 0 0 260px;
  }

  &__content {
    flex: 1 1 auto;
    padding-left: 30px;
  }
}

.menu-page {
  position: relative;
  background: url("../img/main/bg-aside.jpg") no-repeat;

  &__header {
    padding: 20px;
    display: flex;
    align-items: center;
  }

  &__title {
    color: #ffffff;
    font-family: "UniSansHeayBold";
    font-size: 30px;
    letter-spacing: 0.75px;
    line-height: 35/30 * 100%;
  }

  &__burger {
    flex: 0 0 36px; //Щоб не зжимало флекс-елемент
    width: 36px;
    height: 36px;
    background-color: #068292;

    position: relative;
    cursor: pointer;
    padding: 10px 6px;
  }

  &__body {
  }

  &__list {
    li {
      border-bottom: 1px solid #1aa7b9;
      transition: 0.3s background-color linear;
    }

    @media (min-width: $md2+px) {
      li:hover {
        background-color: #098494;
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    padding: 12px 20px;
    letter-spacing: 0.3px;
    font-weight: 300;
    font-size: 12px;
    color: white;
    cursor: pointer;
  }

  &__link--parent {
    &:after {
      content: "";
      background: url("../img/main/strelka.png") no-repeat;
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      width: 14px;
      height: 14px;
    }
  }
}

.menu-page__lines {
  position: relative;
  height: 16px;

  .menu-page__span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: white;

    &:first-child {
      top: 0;
    }

    &:last-child {
      top: auto;
      bottom: 0;
      height: 1.7px;
    }
  }

  &.__active {
    .menu-page__span {
      transform: scale(0);

      &:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px);
      }

      &:last-child {
        transform: rotate(45deg);
        top: calc(50% - 1px);
      }
    }
  }
}


